export default {
	data() {
		return {
			userIsLogin: false,
			userInfo: {}
		}
	},
	methods: {
		turnLogin() {
			uni.navigateTo({
				url: '../registLogin/registLogin'
			});
		},
		setting() {
			uni.navigateTo({
				url: '../meInfo/meInfo'
			});
		}
	},
	onShow() {
		// 判断用户是否登录
		const userInfo = this.getGlobalUser("globalUser");
		if (userInfo != null) {
			this.userIsLogin = true;
			this.userInfo = userInfo;
		} else {
			this.userIsLogin = false;
			this.userInfo = {};
		}
	}
}
